<template lang='pug'>
  
  .container
    .container__row
      img(:src='require("@/assets/img/cb_block_img.png")').container__main-img
      .container__right-side
        h1.container__title Регистрация аккаунта
        p.container__subtitle Создайте новый аккаунт, чтобы получить доступ к личному кабинету и возможности создавать задачи. #[br] Уже зарегистрированы? #[router-link(to='/login') Войдите в аккаунт!]

        .auth
          .auth__data
            input(type='email' placeholder='E-mail' v-model.trim='form.email').auth__input
            input(type='password' placeholder='Пароль' v-model.trim='form.password').auth__input
            input(type='password' placeholder='Повторите пароль' v-model.trim='form.repeatPassword').auth__input
          button(@click='login' :class='{"container__submit_disabled": loading}').container__submit.
            #[img(:src='require("@/assets/img/loading_white.svg")' v-show='loading')] {{ loading ? "Загрузка..." : "Продолжить" }}

</template>

<script>
  
  import {required, email, sameAs} from 'vuelidate/lib/validators'

  export default {
    data() {
      return {
        form: {
          email: '',
          password: '',
          repeatPassword: ''
        },
        loading: false
      }
    },
    validations: {
      form: { email: {required, email}, password: {required}, repeatPassword: {sameAsPassword: sameAs('password')} }
    },
    methods: {
      login() {
        if (this.loading)
          return this.$toast.error('Подождите, идет загрузка...')
        
        this.$v.$touch()

        if (this.$v.$error)
          return this.$toast.error('Заполните все поля корректно!')

        else if (this.form.password.length < 6)
          return this.$toast.error('Минимальная длина пароля - 6 символов')

        this.loading = true

        this.$store.dispatch('userRegister', this.form)
          .then(() => {
            this.$toast.success('Успешная регистраиця')
            this.$router.replace('/tasks').catch(() => {})
          })
          .catch(e => this.$toast.error(e.message))
          .finally(() => this.loading = false)

      }
    }
  }

</script>

<style lang='sass'> @import '@/assets/css/common/auth' </style>